
import { MDBBtn, MDBIcon } from 'mdb-vue-ui-kit'
import { defineComponent } from 'vue'

import router from '@/router'

export default defineComponent({
  name: 'BackButton',
  components: {
    MDBBtn,
    MDBIcon,
  },
  setup() {
    const goBack = (): void => {
      router.back()
    }
    return {
      goBack,
    }
  },
})
