    CartCheckAll,
    CartTotal,
    CartList,
    CartBreadcrumb,
    MDBContainer,
    MDBRow,
    MDBCol,
  },
  setup() {
    const isEmptyCart = CartApi.getIsEmptyCart()
    const isHaveCheckedInCart = CartApi.getIsHaveCheckedInCart()
